html,
body {
  background-color: #121212;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffc069;
}

.timer {
  text-align: center;
}


.box {
  font-weight: 300;
  padding: 10px;
}

.box p {
  font-size: 2.5rem;
  margin: 0;
}

.text {
  font-size: 1.5rem
}

#day, #hour, #minute, #second{
  font-weight: 500;
}
